.card-item {
    width: 170px;
    height: 200px;
    box-sizing: border-box;
    margin: 4px;
    display: inline-grid;
    h4 {
        text-align: center;
        margin: 0;
        font-size: 24px;
        letter-spacing: 0;
    }
    p {
        text-align: center;
        margin: 0;
    }
    .card-img {
        display: block;
        width: 120px;
        height: 120px;
        border-radius: 50%;
        margin: 4px auto;
        box-shadow: -1px 2px 3px rgba(0, 0, 0, 0.1) inset;
    }
    .tge {
        background-image: url("../img/people/tge.jpg");
        background-size: 100% auto;
    }

    .bl {
        background-image: url("../img/people/bl.png");
        background-size: 100% auto;
    }

    .yhw {
        background-image: url("../img/people/yhw.jpg");
        background-size: 100% auto;
    }

    .bqc {
        background-image: url("../img/people/bqc.jpg");
        background-size: 100% auto;
    }
}
