@font-face {
    font-family: "fontawesome";
    src: url("./fonts/fontawesome-webfont.woff");
}

@font-face {
    font-family: "freightsans";
    src: url("./fonts/freightsans.woff");
}

@font-face {
    font-family: "freightsanslight";
    src: url("./fonts/freightsanslight.woff");
}

::-webkit-scrollbar {
    width: 8px;
    margin-right: 2px;
}

::-webkit-scrollbar:horizontal {
    height: 10px;
    margin-bottom: 2px;
}

::-webkit-scrollbar-track {
    border-radius: 10px;
}

::-webkit-scrollbar-track-piece {
    background-color: #cdcdcd;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    width: 6px;
    border-radius: 5px;
    background: #8f8f8f;
    margin-right: 3px;
}

html,
body {
    margin: 0;
    padding: 0;
    background: #fdfdfd;
}

body {
    overflow-y: scroll;
}

header {
    height: 80px;
    display: flex;
    box-sizing: border-box;
    padding: 0 16%;
    width: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 999;
    background: #fdfdfd;
    border-bottom: 1px solid #999;
}

p {
    color: #333;
    font: 400 16px/1.5 "freightsanslight", "Lato", "Helvetica Neue", Helvetica, Arial, sans-serif;
    line-height: 28px;
    font-size: 20px;
}

h1,
h2,
h3,
h4,
h5 {
    color: #000;
    font-family: "freightsanslight", "Lato", "Helvetica Neue", Helvetica, Arial, sans-serif;
    letter-spacing: 3px;
    font-weight: 200;
    margin: 30px 0;
}

h1 {
    font-size: 56px;
}

h2 {
    font-size: 50px;
}

h3 {
    font-size: 40px;
}

h4 {
    font-size: 28px;
}

h5 {
    font-size: 24px;
}

.container {
    width: 100%;
    box-sizing: border-box;
    padding: 80px 0;
    overflow: hidden;
    min-height: calc(100vh - 202px);
}
