.fig-cap-container {
    display: flex;
    align-items: stretch;
    margin-top: 1em;
    margin-bottom: 1em;
    flex-flow: row wrap;
    width: 100%;
    overflow: auto;
    .figure {
        margin: 1em 10px 1em 0;
        text-align: justify;
        background-color: white;
        img {
            max-width: 786px;
            height: 160px;
        }
    }
    .cap-code {
        flex: 1.618 1 0;
        margin: 0;
        min-width: 350px;
    }
    .cap-text {
        box-sizing: border-box;
        padding: 10px;
        flex: 1.618 1 0;
        margin: 0 0 0 10px;
        text-align: justify;
        min-width: 350px;
        background: #f8f8f8;
    }
}
