.sidebar-wrapper {
    position: fixed;
    display: block;
    width: 200px;
    flex: 0 0 auto;
    margin-top: 1.7em;
    padding-right: 0.2em;
    .sidebar-nav {
        display: block;
        padding: 0;
        margin: 0;
        list-style-type: disc;
        .nav-item {
            display: block;
            text-align: -webkit-match-parent;
            a {
                cursor: pointer;
                display: block;
                padding: 0.5em 0;
                color: #666;
                text-decoration: none;
                text-transform: capitalize;
                &:hover {
                    color: #4682b4;
                }
            }
        }
        .sub-nav {
            padding-left: 0px;
            list-style-type: circle;
            margin-block-start: 0px;
            margin-block-end: 0px;
            display: block;
            .nav-item {
                padding-left: 1em;
                padding-top: 0.25em;
                border-left: 2px solid #eee;
                line-height: 24px !important;
            }
        }
    }
}
