.feature-container {
    width: 100%;
    // height: 300px;
    box-sizing: border-box;
    padding: 0 16%;
    margin-top: 70px;
    h4 {
        width: 100%;
        text-align: center;
        margin: 0;
    }
    .split {
        float: left;
        width: 120px;
        height: 1px;
        border-top: 1px solid #aaa;
        margin-left: calc(50% - 60px);
    }
    .feature-inner-container-row {
        width: 100%;
        display: flex;
    }
    .feature-inner-container-col {
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    .feature-inner-container-row,
    .feature-inner-container-col {
        .sub-feature-inner-container {
            flex: 1;
            padding: 30px;
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            p{
                text-align: center;
            }
            h5 {
                text-transform: uppercase;
                text-align: center;
                margin: 6px 0;
            }
            .img-div {
                width: 220px;
                height: 220px;
                border-radius: 50%;
                margin: 20px auto;
                background-size: 100%;
                // box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.1) inset;
            }
            .split {
                margin: 0;
                width: 100%;
            }
        }
        .feature-illus {
            margin: 0 auto;
            height: 200px;
            img,
            video {
                height: 100%;
            }
        }
        .feature-text {
            flex: 1;
            box-sizing: border-box;
            padding: 0;
            text-align: center;
        }
    }
}
