.tutorial-wrapper {
    display: flex;
    flex-direction: column;
    flex-flow: initial;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.content-wrapper {
    margin-left: 210px;
    padding-bottom: 3em;
    flex: 1 1 auto;

    h1,
    h2,
    h3,
    h4 {
        margin: 0.7em 0 0.3em 0;
        text-transform: capitalize;
    }

    h1 {
        vertical-align: middle;
        color: black;
        display: inline-block;
        font-weight: 300;
        font-size: 2.5em;
        margin: 0.7em 0 0.3em 0;
    }

    h2 {
        font-size: 1.75em;
        font-weight: 500;
        border-bottom: 1px solid #cccccc;
        color: black;
        margin-top: 1.5em;
        padding-bottom: 1px;
    }

    h3 {
        font-size: 1.5em !important;
        color: black;
        margin-top: 1.5em;
    }

    p {
        color: #373a3c;
        display: block;
        text-align: justify;
    }
    
    li p {
        margin: 0.2em 0;
    }

    code {
        margin: 0 2px;
        padding: 0 4px;
        background: #f6f6f6;
        border: 1px solid #ddd;
        font-family: monospace;
        font-size: 0.8em;
    }

    pre {
        line-height: 1em;
        background: #f9f9f9;
        border: 1px solid #eee;
        padding: 0px 10px;
        display: block;
        font-family: monospace;
        white-space: pre;
        margin: 1em 0px;
        flex: 1;
        text-align: left;
        code {
            margin: 0;
            padding: 0;
            line-height: 1.2em;
            white-space: pre-wrap;
            border: none;
            background: transparent;
            font-family: monospace;
        }
    }

    em {
        font-weight: bold;
    }

    table {
        width: 100%;
        padding: 0;
        border-collapse: collapse;
        display: table;
        border-spacing: 2px;
        border-color: grey;
        tr {
            border-top: 1px solid #cccccc;
            background-color: white;
            margin: 0;
            padding: 0;
            th {
                font-weight: bold;
                border: 1px solid #cccccc;
                text-align: left;
                margin: 0;
                padding: 6px 13px;
            }
            td {
                border: 1px solid #cccccc;
                text-align: left;
                margin: 0;
                padding: 6px 13px;
            }
            th:first-child,
            td:first-child {
                margin-top: 0;
            }
            &::nth-child(2n) {
                background-color: #f8f8f8;
            }
        }
    }
    a {
        cursor: pointer;
        text-decoration: none;
        color: black;
    }

    table tr td a,
    p a {
        cursor: pointer;
        color: #4682b4;
        &:hover {
            text-decoration: underline;
        }
    }
}
