footer {
    width: 100%;
    height: 80px;
    box-sizing: border-box;
    padding: 0 16%;
    margin-top: 100px;
    .footer-content {
        width: 100%;
        border-top: 1px solid #3a3843;
        padding: 40px;
        box-sizing: border-box;
        text-align: center;
        span {
            color: #7c7c7c;
        }
        a {
            text-decoration: none;
            color: #007acd;
        }
    }
}
